export default{ 
	props:{
		src:{
			type:String,
			default:''
		},
		play:{
			type:Boolean,
			default:false
		},
		short:{
			type:Boolean,
			default:false
		},
		height:{
			type:String,
			default:''
		},
		width:{
			type:String,
			default:''
		},
		duration:{
			type:Number,
			default:0
		}
	},
	data(){
		return{
			time:0
		}
	},
	methods:{
		timeupdate(event){
			if(this.time>=this.duration) this.time=0
			this.time = this.time + 0.25 
			
		},
		clickVideo(){
			this.$emit('click')
		},
		videoPlay(){
			if(this.play){
				this.videoCtx = uni.createVideoContext(`video_${this.src}`,this);
				this.videoCtx.play();
			}else{
				this.videoCtx = uni.createVideoContext(`video_${this.src}`,this);
				this.videoCtx.pause();
			}
			
		}
	},
	watch:{
		play(newVal,oldVal){
			this.videoPlay()
		}
	},
	computed:{
		barWidth(){
			let width = this.time/this.duration*parseInt(this.width)
			return `${width}px`
		}
	}
}
